import './App.css';
import axios from 'axios';
import { useEffect, useState } from 'react';

// const BASE_URL = 'http://localhost:8000/'
const BASE_URL = 'https://csgo-site.herokuapp.com/'

function App() { 
  const [response, setResponse] = useState({});
  const [isLogin, setLogin] = useState(false);
  function login() {
    window.location.href = `${BASE_URL}accounts/steam/login/`
  }

  function logout() {
    axios.get(`${BASE_URL}accounts/logout/`, {withCredentials: true}).then(() => {
      setLogin(false);
      setResponse({});
    }).catch(() => {
      setLogin(false);
      setResponse({});
    })
    }

  function getCookie(name) {
  let matches = document.cookie.match(new RegExp(
    "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
  ));
  return matches ? decodeURIComponent(matches[1]) : undefined;
}

  useEffect(() =>{
    getInfo();
  },[])

    function getInfo() { 
    axios.get(`${BASE_URL}api/me/`, {withCredentials: true}).then((resp) => {
      setResponse(resp.data);
      setLogin(true);
    }).catch(() => {
      setLogin(false)})
   }

  return (
    <>
    <div className="App">
      <div className="header">
        <img className="logo" src="images\NsDqWRymyZE1(1)1.png" />
        <div className="menu">
          <span className="text">Продать скины</span>
          <span className="text">Отзывы</span>
          <span className="text">Личный кабинет</span>
        </div>
        <div className="auth">
          <img src="images/Group.png" onClick={login} className="login-btn"/>
          {!isLogin && <span className="text" onClick={login}>SIGN IN</span>}
          {isLogin && <span className="text" onClick={logout}>LOGOUT</span>}
        </div>
      </div>
      <img className="img" src="/images/mossawi_192164419218_20170410164335_14843.png"/>
      <div className="background"/>
      <div className="modal">
        <div className="modal-background" />
        <div className="modal-content">
          <p className="modal-text">АВТОРИЗУЙТЕСЬ ЧЕРЕЗ
            STEAM
            ДЛЯ ТОГО, ЧТОБЫ ОБМЕНЯТЬ ПРЕДМЕТЫ CS:GO НА ДЕНЬГИ.
          </p>
          <button className="trade-btn">
            Обменять
          </button>
        </div>
      </div>
    </div>
    <div className="footer">
      <img className="footer-iamge" src="images/image1.png"/>
    </div>
    </>
  );
}

export default App;
